@import url('../../assets/constants/Colors.css');



/* UNIVERSAL SCROLLBAR */

*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

*::-webkit-scrollbar-track {
  border-radius: 25px;
}

*::-webkit-scrollbar-thumb {
  background-color: var(--light-grey); 
  border-radius: 25px;
}

*::-webkit-scrollbar-thumb:hover {
  color: var(--white);
  cursor: pointer;
}

/* ***** ***** ***** ***** ***** */

a {
  text-decoration: none;
}